





































import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {}
})
export default class LotteryMusic extends Vue {
  musicInit() {
    const music: any = document.querySelector("#music");

    let rotated = 0;
    let stopAnimate = false;
    const musicBox: any = document.querySelector("#musicBox");

    function animate() {
      requestAnimationFrame(function () {
        if (stopAnimate) {
          return;
        }
        rotated = rotated % 360;
        musicBox.style.transform = "rotate(" + rotated + "deg)";
        rotated += 1;
        animate();
      });
    }

    musicBox.addEventListener(
      "click",
      function (e) {
        if (music.paused) {
          music.play().then(
            () => {
              stopAnimate = false;
              animate();
            },
            () => {
              console.log('');
              // alert('')
            }
          );
        } else {
          music.pause();
          stopAnimate = true;
        }
      },
      false
    );

    setTimeout(function () {
      musicBox.click();
    }, 1000);
  }
  mounted () {
    this.musicInit();
  }
}
